interface DataItem {
  name: string
  value: any
}

const DealUnitMap: { [key: string]: string } = {
  outPriceInDollar: '行权价格 ($)',
  inPriceInDollar: '行权成本 ($)',
  outCount: '行权数量',
  exchangeRate: '美元/人民币汇率',
  baseTax: '本财政年度累计已纳税总额 (RMB)',
  baseEarning: '当前财政年度已有计税收益 (RMB)',
  toCash: '获取现金还是股票',
  outForCost: '系统计算保本售出卖出数量(用于支付成本, 仅适用获取股票)',
}

const DealResultMap: { [key: string]: string } = {
  outIncome: '行权总额',
  inCost: '行权成本',
  taxRate: '适用税率',
  tax: '个税总额',
  quickDeduction: '速算扣除数',
  secFee: 'SEC费用',
  commissionFee: '券商佣金',
  cpmFee: 'CPM手续费(行权手续费)',
  toStockFee: '转股费',
  feeTotal: '总行权费用(券商佣金、 SEC 费用、行权手续费)',
  cash: '净收益现金',
  stock: '净收益股票',
  incomeRate: '净收益比例',
}

function handleBoolean(value: any): any {
  if (value === true) {
    return '现金'
  } else if (value === false) {
    return '股票'
  } else {
    return value
  }
}

const moneyKeys = [
  'outIncome',
  'inCost',
  'tax',
  'quickDeduction',
  'secFee',
  'computeSecFee',
  'commissionFee',
  'cpmFee',
  'toStockFee',
  'feeTotal',
  'cash',
]
function handleMoney(key: string, value: any, exchangeRate: number): any {
  if (moneyKeys.includes(key)) {
    const rmb = (value as number).toFixed(2)
    const dollar = ((value as number) / exchangeRate).toFixed(2)
    return `${rmb} RMB / ${dollar} $`
  } else {
    return value
  }
}

