  // private readonly aliasName!: string | null

  /**************************************************************************
   * data
   **************************************************************************/

  // private dataName: number = 0

  /**************************************************************************
   * computed
   **************************************************************************/

  get dealInput(): DataItem[] {
    const { deal } = this
    const keys = Object.keys(DealUnitMap)
    const tableData = keys.map(item => {
      return {
        name: DealUnitMap[item],
        value: handleBoolean(deal[item as keyof DealUnit]),
      }
    })

    return tableData
  }

  get dealResult(): DealResult {
    const { deal } = this
    const dealResult = computeDealUnit(deal)
    return dealResult
  }

  get dealOut(): DataItem[] {
    const { dealResult, deal } = this
    const keys = Object.keys(DealResultMap)
    const tableData = keys.map(item => {
      const value = dealResult[item as keyof DealResult]
      return {
        name: DealResultMap[item],
        value: handleMoney(item, value, deal.exchangeRate),
      }
    })

    return tableData
  }

