  get outForCostMax(): number {
    const { computeForm } = this
    return computeForm.outCount
  }

  get isForCash(): boolean {
    const { computeForm } = this
    return !!computeForm.toCash
  }

  get inPriceInDollarMax(): number {
    const { computeForm } = this
    return computeForm.outPriceInDollar
  }

  get baseTaxMax(): number {
    const { computeForm } = this
    return computeForm.baseEarning
  }

  private get dealList(): DealUnitWithId[] {
    const { deals } = this
    return deals.map((item, index) => {
      return {
        deal: item,
        title: `${index + 1}`,
        id: shortid.generate(),
      }
    })
  }

