// 收入 (RMB¥) 边际税率 速算扣除数(RMB¥)
// 不超过36,000元的 3% 0
// 超过36,000元至144,000元的部分 10% 2,520
// 超过144,000元至300,000元的部分 20% 16,920
// 超过300,000元至420,000元的部分 25% 31,920
// 超过420,000元至660,000元的部分 30% 52,920
// 超过660,000元至960,000元的部分 35% 85,920
// 超过960,000元的部分 45% 181,920

interface IncomeTaxStepItem {
  limit: number
  taxRate: number
  quickDeduction: number
}

const IncomeTaxSteps: IncomeTaxStepItem[] = [
  {
    limit: 0,
    taxRate: 0,
    quickDeduction: 0,
  },
  {
    limit: 36000,
    taxRate: 0.03,
    quickDeduction: 0,
  },
  {
    limit: 144000,
    taxRate: 0.1,
    quickDeduction: 2520,
  },
  {
    limit: 300000,
    taxRate: 0.2,
    quickDeduction: 16920,
  },
  {
    limit: 420000,
    taxRate: 0.25,
    quickDeduction: 31920,
  },
  {
    limit: 660000,
    taxRate: 0.3,
    quickDeduction: 52920,
  },
  {
    limit: 960000,
    taxRate: 0.35,
    quickDeduction: 85920,
  },
  {
    limit: Number.MAX_VALUE,
    taxRate: 0.45,
    quickDeduction: 181920,
  },
]

interface DealUnit {
  //  行权价格
  outPriceInDollar: number
  // 行权成本
  inPriceInDollar: number
  // 行权数量
  outCount: number
  //  美元/人民币汇率
  exchangeRate: number
  // 本财政年度累计已纳税总额
  baseTax: number
  // 当前财政年度已有计税收益
  baseEarning: number
  // 获取现金还是股票
  toCash: boolean
  // 系统计算保本售出卖出数量(用于支付成本)
  // 仅适用获取股票
  outForCost: number
}

interface DealResult {
  // 行权总额
  outIncome: number
  // 行权成本
  inCost: number
  // 适用税率
  taxRate: number
  // 个税总额
  tax: number
  // 速算扣除数
  quickDeduction: number
  // SEC费用
  secFee: number
  // 券商佣金
  commissionFee: number
  // CPM手续费(行权手续费)
  cpmFee: number
  // 转股费
  toStockFee: number
  // 总行权费用(券商佣金、 SEC 费用、行权手续费)
  feeTotal: number
  // 净收益现金
  cash: number
  // 净收益股票
  stock: number
  // 净收益比例
  incomeRate: number
}

function computeTaxAndQuickDeduction(
  earning: number,
  baseEarning: number,
): { taxRate: number; quickDeduction: number } {
  let taxRate = 0
  let quickDeduction = 0
  const total = earning + baseEarning
  for (let index = 1; index < IncomeTaxSteps.length; index++) {
    const prev = IncomeTaxSteps[index - 1]
    const current = IncomeTaxSteps[index]
    if (total > prev.limit && total < current.limit) {
      taxRate = current.taxRate
      quickDeduction = current.quickDeduction
    }
  }
  return {
    taxRate,
    quickDeduction,
  }
}

// 计算股权激励收入
// 股权激励收入= （行权时股票出售价格 – 授予价格） x 行权股数 x 汇率
function computeIncomeOfShare({
  outPriceInDollar,
  inPriceInDollar,
  outCount,
  exchangeRate,
}: DealUnit): number {
  return (outPriceInDollar - inPriceInDollar) * outCount * exchangeRate
}

// 综合所得税率表计算税金
// 应纳税额＝股权激励收入×适用税率－速算扣除数
function computeTax(
  deal: DealUnit,
): {
  tax: number
  taxRate: number
  quickDeduction: number
} {
  const earning = computeIncomeOfShare(deal)
  const taxAndQuick = computeTaxAndQuickDeduction(earning, deal.baseEarning)
  const tax =
    taxAndQuick.taxRate * (earning + deal.baseEarning) -
    taxAndQuick.quickDeduction -
    deal.baseTax
  return {
    tax,
    taxRate: taxAndQuick.taxRate,
    quickDeduction: taxAndQuick.quickDeduction,
  }
}

// SEC费用
// 按照出售收益收取，美元0.0207/$1000.00 股票出售后美国证监会收
// 取的费用
function computeSecFee({
  outPriceInDollar,
  inPriceInDollar,
  exchangeRate,
  outCount,
}: DealUnit): number {
  return (
    ((outPriceInDollar - inPriceInDollar) * outCount * 0.0207 * exchangeRate) /
    1000
  )
}

// 券商佣金
// 按照出售股数收取，每股美元$0.02
// 每笔交易最低收费为美元$20.00
// 券商在出售股票时收取的 费用
function computeCommission({ outCount, exchangeRate }: DealUnit): number {
  const commission = outCount * 0.02
  const mini = 20
  return Math.max(commission, mini) * exchangeRate
}

// CPM手续费
// 每笔行权/股票出售交易收取美元$5.35 Computershare收取的手续费

function computeCPM({ exchangeRate }: DealUnit): number {
  return 5.35 * exchangeRate
}

// 转股费 每股美元$0.01，每笔转股最低收取美元
// $100.00
// 把行权获得的股票转出至本人股票账户的手续费
function computeStocklizeFee({ outCount, exchangeRate }: DealUnit): number {
  const fee = 0.01 * outCount
  return Math.max(fee, 100) * exchangeRate
}

function computeToCash(deal: DealUnit): DealResult {
  const { outPriceInDollar, inPriceInDollar, outCount, exchangeRate } = deal
  const outIncome = outPriceInDollar * outCount * exchangeRate
  const inCost = inPriceInDollar * outCount * exchangeRate
  const total = (outPriceInDollar - inPriceInDollar) * outCount * exchangeRate
  const secFee = computeSecFee(deal)
  const commissionFee = computeCommission(deal)
  const cpmFee = computeCPM(deal)
  const toStockFee = 0
  const feeTotal = secFee + commissionFee + cpmFee + toStockFee
  const taxResult = computeTax(deal)
  const { tax, taxRate, quickDeduction } = taxResult
  const cash = total - tax - feeTotal
  const incomeRate = cash / total
  return {
    outIncome,
    inCost,
    secFee,
    commissionFee,
    cpmFee,
    toStockFee,
    feeTotal,
    tax,
    taxRate,
    quickDeduction,
    cash,
    stock: 0,
    incomeRate,
  }
}

function computeToStock(deal: DealUnit): DealResult {
  const {
    outPriceInDollar,
    inPriceInDollar,
    outCount,
    exchangeRate,
    outForCost,
  } = deal
  const outIncome = outPriceInDollar * outCount * exchangeRate
  const inCost = inPriceInDollar * outCount * exchangeRate
  const secFee = computeSecFee(deal)
  const commissionFee = computeCommission(deal)
  const cpmFee = computeCPM(deal)
  const toStockFee = computeStocklizeFee(deal)
  const feeTotal = secFee + commissionFee + cpmFee + toStockFee
  const taxResult = computeTax(deal)
  const { tax, taxRate, quickDeduction } = taxResult
  const total = (outPriceInDollar - inPriceInDollar) * outForCost * exchangeRate
  const cash = total - tax - feeTotal
  const incomeRate = 1 - (tax + feeTotal) / (exchangeRate * outCount)
  return {
    outIncome,
    inCost,
    secFee,
    commissionFee,
    cpmFee,
    toStockFee,
    feeTotal,
    tax,
    taxRate,
    quickDeduction,
    cash,
    stock: outCount - outForCost,
    incomeRate,
  }
}

function computeDealUnit(deal: DealUnit): DealResult {
  if (deal.toCash) {
    return computeToCash(deal)
  }
  return computeToStock(deal)
}

export { computeDealUnit, DealUnit, DealResult }
